import React from 'react'

import {Demo, DemoTitle, DemoHelp, DemoContent, DemoProp, DemoProps} from '../components/demo'
import ProfileCard from '../../lib/layout/ProfileCard'

import Button from '@material-ui/core/Button'
import Tooltip from '@material-ui/core/Tooltip'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import SwapHorizIcon from '@material-ui/icons/SwapHoriz'
import ListItemText from '@material-ui/core/ListItemText'
import ListItem from '@material-ui/core/ListItem'
import SettingsIcon from '@material-ui/icons/Settings'
import HelpIcon from '@material-ui/icons/Help'
import List from '@material-ui/core/List'
import ScreenSize from '../../lib/ScreenSize'

type State = {
    // variant
}

export default class ProfileCardDemo extends React.PureComponent<{}, State>{
    state = {
        anchorEl: null,
        identity: {
            license: '2947b4ae-0d40-447b-886f-dcd3aa11648b;693081080913543;1198',
            identity: {
                id: '2947b4ae-0d40-447b-886f-dcd3aa11648b'
            },
            client: {
                id: '693081080913543',
                name: 'Vrdal & Shared Employees & Co'
            },
            user: {
                id: '1198'
            },
            profile: {
                id: '2947b4ae-0d40-447b-886f-dcd3aa11648b',
                firstName: 'Sjef',
                lastName: 'Sjefesenen',
                identifier: 'sjef@kpdemo.no',
                culture: 'nn-NO',
                language: 'NO',
                timeZone: 'Europe/Copenhagen',
                country: 'ES',
                created: '2008-08-04T09:04:36.913+00:00',
                modified: '2019-03-12T00:53:56.946+00:00',
                gender: 'Male',
                birthDate: '2018-11-20',
                location: 'Disneyland',
                locationCoordinates: '33.8120918,-117.91897419999998',
                visibility: 'Public',
                thumb: {data: 'data:image/png;base64, '}
            },
            locale: {
                country: 'NO',
                culture: 'nn-NO',
                language: 'NO',
                timeZoneOffset: '01:00:00',
                timeZone: 'Europe/Copenhagen'
            }
        }
    }

    handleClick = event => {
        this.setState({
            anchorEl: event.currentTarget
        })
    }

    onSignOut = () => {
        console.log('sign out')
    }

    onSwitch = () => {
        console.log('switch ')
    }

    onClient = () => {
        console.log('client click')
    }

    onClose = () => {
        this.setState({
            anchorEl: null,
        })
    }

    onProfile = () => {
        console.log('onProffil')
    }
    onSettings = () => {
        console.log('on setting')
    }
    onRequests = () => {
        console.log('on request')
    }

    onHelp = () => {
        console.log('on help')
    }

    render(){
        const {anchorEl} = this.state
        const open = Boolean(anchorEl)
        return (
            <Demo>
                <DemoTitle srcPath='ProfileCard.tsx' demoPath='ProfileCardDemo.tsx'>Profile Card</DemoTitle>
                <DemoHelp>Testing</DemoHelp>
                <DemoProps>
                    <DemoProp name='open' type='boolean' default='' description=''/>
                    <DemoProp name='onClose' type='function' default='' description=''/>
                    <DemoProp name='onSignOut' type='function' default='' description=''/>
                    <DemoProp name='signOutText' type='string' description='Text for sign out button'/>
                    <DemoProp name='anchorEl' type='HTMLElement' default='' description='Element to anchors the pop up'/>
                    <DemoProp name='children' type='React.ReactNode' default='' description='ListItems to add to the menu below settings'/>
                </DemoProps>
                <DemoContent>
                    <Button
                        aria-owns={open ? 'simple-popper' : undefined}
                        aria-haspopup="true"
                        variant="contained"
                        onClick={this.handleClick}
                    >
                        View profile
                    </Button>
                    <ProfileCard identity={this.state.identity} signOutText="Sign out" open={open} onClose={this.onClose} onSignOut={this.onSignOut} anchorEl={anchorEl}>
                        <ScreenSize>
                            {({mobile}) => (
                                <List dense={!mobile}>
                                    <ListItem button onClick={this.onProfile}>
                                        <ListItemText inset primary="Profile"/>
                                    </ListItem>
                                    <ListItem button onClick={this.onRequests}>
                                        <ListItemText inset primary="Foresprsler"/>
                                    </ListItem>
                                    <ListItem button divider onClick={this.onSettings}>
                                        <Tooltip title='Profile settings'>
                                            <ListItemIcon>
                                                <SettingsIcon fontSize='small'/>
                                            </ListItemIcon>
                                        </Tooltip>
                                        <ListItemText inset primary="Settings"/>
                                    </ListItem>
                                    {mobile && <ListItem divider button>
                                        <Tooltip title='Switch client'>
                                            <ListItemIcon onClick={this.onSwitch}>
                                                <SwapHorizIcon />
                                            </ListItemIcon>
                                        </Tooltip>
                                        <ListItemText onClick={this.onClient} inset primary={this.state.identity.client.name} secondary='Administrer'/>
                                    </ListItem>}
                                    <ListItem button divider onClick={this.onHelp}>
                                        <Tooltip title='Help me'>
                                            <ListItemIcon>
                                                <HelpIcon fontSize='small'/>
                                            </ListItemIcon>
                                        </Tooltip>
                                        <ListItemText primary="Help"/>
                                    </ListItem>
                                </List>
                            )}
                        </ScreenSize>
                    </ProfileCard>
                </DemoContent>
            </Demo>
        )
    }
}